import { MDBRow, MDBCol } from 'mdbreact';
import React from 'react';
import Graph from '../assets/graph.png';

class Result extends React.Component {

  state = {

  }

  componentDidMount = () => {
  }

  render() {

    const styles = {
      landing: {
        padding: '10vh 15vw 0vh 15vw'
      },
      imageContainer: {
        display: 'flex',
        justifyContent: 'center',
        padding: '4vh 0vh'
      },
      image: {
        height: '45vh'
      }
    };

    return (
      <>
        <div style={styles.landing}>
          <MDBRow>

            <MDBCol md="12">
              <div className="header">
                {"Results"}
              </div>
            </MDBCol>

          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>

            <MDBCol md="12">
              <div className="textHeader font-weight-bold">
                {/* {"Difference between Multimodal and single modal systems"} */}
                {"Lorem Ipsum is simply dummy text of the printing and typesetting industry."}
              </div>
            </MDBCol>

            <MDBCol style={{ paddingTop: '1vh' }} md="12">
              <div className="textDescription">
                {"Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."}
              </div>
            </MDBCol>

            <MDBCol md="12">
              <div style={styles.imageContainer}>
                <img style={styles.image} src={Graph}></img>
              </div>
            </MDBCol>

          </MDBRow>

          <MDBRow style={{ paddingTop: '2vh', paddingBottom: '2vh' }}>

            <MDBCol md="12">
              <div className="textHeader font-weight-bold">
                {/* {"Difference between Multimodal and single modal systems"} */}
                {"Lorem Ipsum is simply dummy text of the printing and typesetting industry."}
              </div>
            </MDBCol>

            <MDBCol style={{ paddingTop: '1vh' }} md="12">
              <div className="textDescription">
                {"Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book."}
              </div>
            </MDBCol>

            <MDBCol md="12">
              <div style={styles.imageContainer}>
                <img style={styles.image} src={Graph}></img>
              </div>
            </MDBCol>

          </MDBRow>

        </div>
      </>
    );
  }
}

export default Result;
